@import url("https://fonts.maateen.me/solaiman-lipi/font.css");
@import url("https://fonts.maateen.me/kalpurush/font.css");

$primaryColor: #005e55;
body {
  font-family: "SolaimanLipi", sans-serif;
}

.battery {
  height: 300px;
  width: 80px;
  background: #ddd;
  position: absolute;
  right: 0;
  border: 5px solid #000;
  top: 50%;
  transform: translateY(-50%);

  @media screen and (max-width: 991px) {
    height: 300px;
    width: 50px;
    background: #ddd;
    position: absolute;
    right: 0;
    border: 5px solid #000;
    top: 0;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: rotate(90deg);
    margin: auto;
    left: 0;
  }
  &:before {
    content: "";

    width: 50px;
    height: 17px;
    background: #000;
    position: absolute;
    top: -21px;
    left: 50%;
    transform: translateX(-50%);

    @media screen and (max-width: 991px) {
      width: 30px;
    }
  }
  .unit {
    width: 100%;
    background: $primaryColor;
    position: absolute;
    bottom: 0;
    transition: 0.5s all;
    .percent {
      width: 158%;
      height: 5px;
      background-color: #111;
      position: absolute;
      top: 0;

      span {
        position: absolute;
        right: -21px;
        background: $primaryColor;
        width: 40px;
        text-align: center;
        height: 40px;
        line-height: 40px;
        border-radius: 40px;
        top: -14px;
        color: #fff;
        font-weight: 800;
      }
    }
  }
}

.intro {
  transition: 0.5s all;

  @media screen and (max-width: 991px) {
    margin-top: 250px;
  }
  h1.title {
    font-size: 49px;
  }
  .title {
    font-family: "Kalpurush", sans-serif;
  }
  .btn-submit {
    display: flex;
    position: relative;
    img {
      width: 40px;
      height: 40px;
      border-radius: 0;
    }
  }
  .btn-intro {
    display: flex;
    border-radius: 0px;
    border: 2px solid $primaryColor;
    overflow: hidden;
    position: relative;
    padding: 8px 35px;

    @media screen and (max-width: 991px) {
      width: 100%;
      justify-content: center;
      margin-bottom: 22px;
    }

    &.btn-submit {
      border: 0px solid;
      padding: 0px;
      &:hover {
        background-color: transparent;
        &:before {
          content: "";
          background-color: transparent;
        }
      }
    }

    &:hover {
      &:before {
        content: "";
        position: absolute;
        background-color: $primaryColor;
        left: 0;
        width: 100%;
        transition: 0.5s all;
        height: 100%;
      }
      color: #fff;
    }
    &:before {
      content: "";
      position: absolute;
      background-color: $primaryColor;
      left: 0;
      width: 0;
      height: 100%;
      transition: 0.5s all;
      z-index: -1;
    }
    img {
      width: 40px;
      height: 40px;
      border-radius: 0;
      position: absolute;
      right: 10px;
    }
  }
}

// Radio Style

.inputGroup {
  background-color: #fff;
  display: block;
  margin: 10px 0;
  position: relative;

  label {
    background-color: #ddd;
    padding: 12px 30px;
    width: 100%;
    display: block;
    text-align: left;
    color: #3c454c;
    cursor: pointer;
    position: relative;
    z-index: 2;
    transition: color 200ms ease-in;
    overflow: hidden;
    @media screen and (max-width: 600px) {
      font-size: 13px;
    }
    &:before {
      width: 100%;
      height: 10px;
      border-radius: 50%;
      content: "";
      background-color: $primaryColor;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%) scale3d(1, 1, 1);
      transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1);
      opacity: 0;
      z-index: -1;
    }

    &:after {
      width: 32px;
      height: 32px;

      @media screen and (max-width: 600px) {
        width: 20px;
        height: 20px;
      }

      content: "";
      border: 2px solid #d1d7dc;
      background-color: #fff;
      background-image: url("data:image/svg+xml,%3Csvg width='32' height='32' viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.414 11L4 12.414l5.414 5.414L20.828 6.414 19.414 5l-10 10z' fill='%23fff' fill-rule='nonzero'/%3E%3C/svg%3E ");
      background-repeat: no-repeat;
      background-position: 2px 3px;
      border-radius: 50%;
      z-index: 2;
      position: absolute;
      right: 30px;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
      transition: all 200ms ease-in;
    }
  }

  input:checked ~ label {
    color: #fff;

    &:before {
      transform: translate(-50%, -50%) scale3d(56, 56, 1);
      opacity: 1;
    }

    &:after {
      background-color: #54e0c7;
      border-color: #54e0c7;
    }
  }

  input {
    width: 32px;
    height: 32px;
    order: 1;
    z-index: 2;
    position: absolute;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    visibility: hidden;
  }
}

.h-sc {
  height: 100vh;
}

#about {
  background: rgba(#fff, 1);
  width: 100%;
  box-shadow: 0px 0px 37px 0px #ddd;
  position: fixed;
  margin: auto;
  padding: 35px;
  height: 100%;
  transition: 1s all;
  z-index: 1;
  display: flex;
  align-items: center;
  font-size: 26px;
  text-align: center;
  transform: scale(0);
  overflow: scroll;
  @media screen and (max-width: 567px) {
    font-size: 14px;
  }
}
#aboutClose {
  position: absolute;
  float: right;
  right: 7%;
  top: 10%;
  border-radius: 100%;
}

#sidebar {
  position: absolute;
  background: #005e55;
  left: 0;
  top: 0;
  min-width: 100px;
  height: 100%;

  @media screen and (max-width: 991px) {
    left: 0;
    top: 0;
    min-width: 100%;
    height: 100px;
    align-items: center;
    display: flex;
    padding: 20px;
  }

  .btn {
    min-width: 150px;
    background: rgba(0, 0, 0, 0.3);
    border: none;
    min-height: 61px;
    border-radius: 0;
    border-bottom: 1px solid darken($color: #005e55, $amount: 30%);
    border-top: 1px solid lighten($color: #005e55, $amount: 10%);
    &:last-child {
      border-bottom: none;
    }
    &:first-child {
      border-top: none;
    }
  }
}

.question-box {
  padding: 20px;
  .title {
    font-size: 26px;
    line-height: 1.7;
  }
}

.badge-primary {
  background: $primaryColor;
}
.bg-primary {
  background-color: $primaryColor !important;
}

.btn-primary {
  background: $primaryColor;
  border: none;
  border-radius: 0;
}

.btn-primary.disabled,
.btn-primary:disabled {
  background-color: $primaryColor;
}
